import { Component, OnInit } from '@angular/core';
import { PlyService } from 'src/app/services/ply.service';
import 'codemirror/mode/julia/julia'
import { MatSnackBar } from '@angular/material/snack-bar';
import { SnackbarComponent } from 'src/app/shared/snackbar/snackbar.component';

@Component({
  selector: 'app-analisis',
  templateUrl: './analisis.component.html',
  styleUrls: ['./analisis.component.css']
})
export class AnalisisComponent implements OnInit {

  options  = {
    lineWrapping: false,
    lineNumbers: true,
    theme: 'yonce',
    mode: "text/x-julia"
  }
  entrada : string = `println("Bienvenido a JOLC");`
  consola : string = ""

  constructor( private plyService : PlyService, private _snackBar: MatSnackBar ) { 
    this.consola = ""
  }

  ngOnInit(): void {
  }

  ejecutar(){
    this.plyService.analizar(this.entrada).subscribe(
      resp => {
        this.consola = resp.text
      }
    );
  }

  compilar(){
    this.plyService.compilar(this.entrada).subscribe(
      resp => {
        this.consola = resp.text
      }
    );
  }

  optimizarMirilla(){
    this.plyService.optimizarMirilla(this.consola).subscribe(
      resp => {
        this.consola = resp.text
      }
    );
  }

  optimizarBloques(){
    this.plyService.optimizarBloques(this.consola).subscribe(
      resp => {
        this.consola = resp.text
      }
    );
  }

  generarReportes(){
    this.plyService.generarReportes(this.entrada).subscribe();
  }

  openSnackBar() {
    this._snackBar.openFromComponent(SnackbarComponent, {
      duration: 1000,
      horizontalPosition : 'right',
      verticalPosition : 'bottom'
    });
  }

  limpiarEntrada(){
    this.entrada = "";
  }

  limpiarConsola(){
    this.consola = "";
  }

}
