
import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { environment } from 'src/environments/environment';
import { Observable } from 'rxjs';
import { textRequest, textResponse } from '../interfaces/flask.interfaces';

@Injectable({
  providedIn: 'root'
})
export class PlyService {

  flaskUrl: string = environment.flaskEndpoint;

  constructor( private http: HttpClient) { }

  analizar(text : string):Observable<textResponse>{
    let req : textRequest = {
      text : text
    }
    let obj = JSON.stringify(req)

    return this.http.post<textResponse>(`${this.flaskUrl}/analizar`, obj);
  }

  compilar( text : string) : Observable<textResponse>{
    let req : textRequest = {
      text : text
    }

    return this.http.post<textResponse>(`${this.flaskUrl}/compilar`, req);
  }

  optimizarMirilla( text : string) : Observable<textResponse>{
    let req : textRequest = {
      text : text
    }

    return this.http.post<textResponse>(`${this.flaskUrl}/mirilla`,req);
  }

  optimizarBloques( text : string) : Observable<textResponse>{
    let req : textRequest = {
      text : text,
    }

    return this.http.post<textResponse>(`${this.flaskUrl}/bloques`,req);
  }

  generarReportes( text : string) : Observable<textResponse>{
    let req : textRequest = {
      text : text, 
    }

    return this.http.post<textResponse>(`${this.flaskUrl}/reportes`,req);
  }
}
